/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

button {
  background: none;
  border: 0; }

a {
  color: #047AE6;
  text-decoration: none; }

* {
  box-sizing: border-box; }

.text {
  font-family: "Open Sans", "Helvetica Neue", Helvetica, sans-serif; }

.title {
