  border-radius: 4px; }

.container {
  max-width: 980px;
  margin: 0 auto;
  padding: 0 10px; }

.grid {
  display: flex;
  flex: 1;
  flex-wrap: wrap; }
  .grid.grid--no-wrap {
    flex-wrap: nowrap; }
  .grid.grid--align-center {
    align-items: center; }
  .grid.grid--justify-end {
    justify-content: flex-end; }
  .grid.grid--space {
    justify-content: space-between; }
  .grid.grid--evenly {
    justify-content: space-evenly; }
  .grid.grid--out {
    margin-right: -10px;
    margin-left: -10px; }
  .grid.grid--column {
    flex-direction: column; }

.grid__head--sticky {
  position: sticky;
  z-index: 2;
  top: 0; }

header.has-user-notification + section .grid__head--sticky {
  top: 124px; }

.grid-cell {
  padding-right: 10px;
  padding-left: 10px; }
  .grid-cell.grid-cell--noMargin {
    margin-bottom: 0; }

.grid-cell--col1 {
  flex: 0 0 8.3334%;
  max-width: 8.3334%; }

.grid-cell--col2 {
  flex: 0 0 16.6667%;
  max-width: 16.6667%; }

.grid-cell--col3 {
  flex: 0 0 25%;
